/* Footer.module.css */



.container {
    width: 100%;
    font-family: sans-serif;
    background-color: white;
  }
  
  .navGrid {
    max-width: 1280px;
    margin: 0 auto;
    padding: 1.5rem;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
  
  @media (min-width: 768px) {
    .navGrid {
      grid-template-columns: repeat(5, 1fr);
    }
  }
  
  .column {
    margin-bottom: 1rem;
  }
  
  .heading {
    font-weight: 600;
    color: #333;
    margin-bottom: 1rem;
    text-transform: uppercase;
    font-size: 1rem;
  }
  
  .text {
    font-size: 0.875rem;
    color: #666;
    line-height: 1.5;
  }
  
  .linkList {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .linkList li {
    margin-bottom: 0.5rem;
  }
  
  .link {
    font-size: 0.875rem;
    color: #666;
    text-decoration: none;
    transition: color 0.2s;
  }
  
  .link:hover {
    color: #333;
  }
  
  .socialSection {
    display: flex;
    align-items: center;
    max-width: 1280px;
    margin: 2rem auto;
    padding: 0 1.5rem;
  }
  
  .followText {
    font-size: 0.875rem;
    color: #666;
    margin-right: 1rem;
  }
  
  .socialIcons {
    display: flex;
    gap: 1rem;
  }
  
  .socialIcon {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background-color: #f5f5f5;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s;
  }
  
  .socialIcon:hover {
    background-color: #e0e0e0;
  }
  
  /* Social icons placeholders - replace with actual icons or icon font */
  .facebookIcon, .twitterIcon, .instagramIcon, .youtubeIcon {
    width: 1rem;
    height: 1rem;
    background-color: #888;
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
  }
  
  .facebookIcon {
    mask-image: url('C:\Users\Shree\OneDrive\Desktop\Godrej\forest-grove\src\Assets\icons\facebook.svg');
  }
  
  .twitterIcon {
    mask-image: url('C:\Users\Shree\OneDrive\Desktop\Godrej\forest-grove\src\Assets\icons\twitter.svg');
  }
  
  .instagramIcon {
    mask-image: url('C:\Users\Shree\OneDrive\Desktop\Godrej\forest-grove\src\Assets\icons\instagram.svg');
  }
  
  .youtubeIcon {
    mask-image: url('C:\Users\Shree\OneDrive\Desktop\Godrej\forest-grove\src\Assets\icons\youtube.svg');
  }
  
  .appSection {
    display: flex;
    justify-content: flex-end;
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 1.5rem;
    gap: 1rem;
  }
  
  .appImage {
    height: 40px;
    width: auto;
  }
  
  .footerBottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1280px;
    margin: 2rem auto;
    padding: 2rem 1.5rem;
    border-top: 1px solid #eaeaea;
  }
  
  .logo {
    height: 40px;
    width: auto;
  }
  
  .copyrightSection {
    display: flex;
    gap: 1rem;
    font-size: 0.75rem;
    color: #888;
  }
  
  .copyright {
    margin-right: 1rem;
  }
  
  .footerLink {
    color: #888;
    text-decoration: none;
  }
  
  .footerLink:hover {
    text-decoration: underline;
  }
  
  .sideVisitButton {
    writing-mode: vertical-rl;
    transform: rotate(180deg);
    padding: 1rem 0.5rem;
    background-color: #f0f0f0;
    font-size: 0.875rem;
    font-weight: 600;
    letter-spacing: 0.1em;
    cursor: pointer;
    position: fixed;
    right: 0;
    top: 50%;
    transform: translateY(-50%) rotate(180deg);
  }