/* PlanViewer.module.css */

.planViewer {
  font-family: sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  padding: 2rem 0;
}

/* Header Styles */
.planHeader {
  text-align: center;
  margin-bottom: 2rem;
}

.planTitle {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1.5rem;
  font-weight: 600;
}



.tabContainer {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  position: relative;
}

.tabContainer::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  height: 1px;
  background-color: #e0e0e0;
}

.tab {
  background: none;
  border: none;
  padding: 0.5rem 1.5rem;
  margin: 0 1rem;
  font-size: 1.25rem;
  color: #aaa;
  cursor: pointer;
  position: relative;
  transition: color 0.3s;
}

.tab:hover {
  color: #666;
}

.activeTab {
  color: #000;
  font-weight: 500;
}

.activeTab::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000;
  z-index: 1;
}

/* Plan Content Styles */
.planContent {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}

/* Master Plan Styles */
.masterPlanContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.masterPlanItem {
  width: 100%;
  max-width: 1000px;
}

.masterPlanInfo {
  text-align: center;
  margin-bottom: 2rem;
}

.location {
  font-size: 1rem;
  color: #666;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
}

.masterPlanTitle {
  font-size: 1.5rem;
  color: #8bc34a;
  margin-bottom: 0.5rem;
  font-weight: normal;
}

.projectName {
  font-size: 1.75rem;
  color: #666;
  margin-bottom: 1rem;
}

.masterPlanImage {
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s;
}

.masterPlanImage:hover {
  transform: scale(1.02);
}

.masterPlanImage img {
  width: 100%;
  height: auto;
  display: block;
}

/* Floor Plans Styles */
.floorPlansContainer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.floorPlanItem {
  flex: 1;
  min-width: 300px;
  max-width: calc(33.33% - 1rem);
}

.floorPlanTitle {
  font-size: 0.875rem;
  color: #666;
  margin-bottom: 0.5rem;
}

.floorPlanImage {
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s;
}

.floorPlanImage:hover {
  transform: scale(1.05);
}

.floorPlanImage img {
  width: 100%;
  height: auto;
  display: block;
}

/* Unit Plans Styles */
.unitPlansContainer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.unitPlanItem {
  flex: 1;
  min-width: 300px;
  max-width: calc(33.33% - 1rem);
}

.unitPlanTitle {
  font-size: 0.875rem;
  color: #666;
  margin-bottom: 0.5rem;
}

.unitPlanImage {
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s;
}

.unitPlanImage:hover {
  transform: scale(1.05);
}

.unitPlanImage img {
  width: 100%;
  height: auto;
  display: block;
}

/* Navigation Styles */
.navigation {
  position: absolute;
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}

.navArrow {
  width: 40px;
  height: 40px;
  border: none;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: auto;
}

.arrowLeft,
.arrowRight {
  width: 12px;
  height: 12px;
  border-top: 2px solid #666;
  border-right: 2px solid #666;
}

.arrowLeft {
  transform: rotate(-135deg);
  margin-left: 4px;
}

.arrowRight {
  transform: rotate(45deg);
  margin-right: 4px;
}

/* Modal Styles */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modalOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
}

.modalContent {
  position: relative;
  width: 90%;
  height: 90%;
  z-index: 1001;
  display: flex;
  align-items: center;
  justify-content: center;
}

.closeButton {
  position: absolute;
  top: -40px;
  right: 0;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.closeIcon {
  line-height: 1;
}

.modalImageContainer {
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
}

.modalImage {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  display: block;
}

/* Utility to prevent body scrolling */
.noScroll {
  overflow: hidden;
}

/* Schedule a visit button */
.scheduleVisit {
  position: fixed;
  right: 0;
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
  writing-mode: vertical-rl;
  background-color: #f0f0f0;
  padding: 1rem 0.5rem;
  font-size: 0.875rem;
  font-weight: 600;
  letter-spacing: 0.1em;
  cursor: pointer;
  z-index: 10;
}

/* Media Queries */
@media (max-width: 768px) {
  .floorPlanItem,
  .unitPlanItem {
    max-width: 100%;
  }
  
  .tab {
    font-size: 1rem;
    margin: 0 0.5rem;
    padding: 0.5rem 0.75rem;
  }
  
  .navigation {
    display: none;
  }
}
@media (max-width: 1024px) {
  .planViewer {
    padding: 1rem 0;
  }

  .tab {
    font-size: 1.1rem;
    padding: 0.5rem 1rem;
  }

  .floorPlansContainer, .unitPlansContainer {
    grid-template-columns: repeat(2, 1fr); /* Two columns instead of three */
  }
}

@media (max-width: 768px) {
  .floorPlansContainer, .unitPlansContainer {
    grid-template-columns: repeat(1, 1fr); /* Single column for mobile */
  }

  .tab {
    font-size: 1rem;
    margin: 0 0.5rem;
    padding: 0.5rem 0.75rem;
  }

  .planTitle {
    font-size: 1.25rem;
  }

  .masterPlanTitle {
    font-size: 1.25rem;
  }

  .navigation {
    display: none;
  }

  .modalContent {
    width: 95%;
    height: auto;
    padding: 1rem;
  }

  .modalImage {
    max-width: 90%;
    max-height: 80vh;
  }

  .scheduleVisit {
    font-size: 0.75rem;
    padding: 0.75rem 0.5rem;
  }
}
