.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 50px 20px;
    font-family: 'Arial', sans-serif;
    color: #555;
    text-align: center;
  }
  
  .galleryHeading {
    font-size: 18px;
    font-weight: 400;
    color: #333;
    text-transform: uppercase;
    position: relative;
    display: inline-block;
    margin-bottom: 30px;
    letter-spacing: 1.5px;
  }
  
  .galleryHeading::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 1px;
    background-color: #ccc;
  }
  
  .description {
    font-size: 22px;
    color: #555;
    max-width: 800px;
    margin: 0 auto 50px;
    line-height: 1.6;
    font-weight: 300;
  }
  
  .galleryGrid {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 30px;
    flex-wrap: wrap;
  }
  
  .galleryItem {
    display: flex;
    flex-direction: column;
    max-width: 370px;
  }
  
  .galleryImage {
    width: 100%;
    height: auto;
    border-radius: 4px;
    overflow: hidden;
    transition: transform 0.3s ease;
  }
  
  .galleryImage:hover {
    transform: scale(1.02);
  }
  
  .imageCaption {
    font-size: 14px;
    color: #777;
    margin-top: 10px;
    text-align: left;
    font-weight: 300;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin: 20px 0 30px;
  }
  
  .dot, .activeDot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    display: inline-block;
  }
  
  .dot {
    background-color: #ddd;
  }
  
  .activeDot {
    background-color: #333;
    width: 30px;
    border-radius: 4px;
  }
  
  .viewGalleryContainer {
    margin-top: 20px;
  }
  
  .viewGalleryButton {
    background-color: transparent;
    border: 1px solid #333;
    color: #333;
    font-size: 14px;
    padding: 12px 30px;
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .viewGalleryButton:hover {
    background-color: #333;
    color: white;
  }
  /* Modal Background */
.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal Box */
.modalContent {
  background: white;
  padding: 20px;
  width: 80%;
  max-width: 600px;
  border-radius: 10px;
  position: relative;
  text-align: center;
}

/* Close Button */
.closeButton {
  position: absolute;
  top: 10px;
  right: 15px;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

/* Video Grid */
.videoGrid {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

  
  @media (max-width: 768px) {
    .galleryGrid {
      flex-direction: column;
      align-items: center;
    }
    
    .galleryItem {
      max-width: 100%;
    }
    
    .description {
      font-size: 18px;
      padding: 0 20px;
    }
  }