.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 50px 20px;
  font-family: 'Arial', sans-serif;
  color: #555;
  position: relative;
  background-color: #f7f2eb;
  padding-bottom: 80px;
}

.header {
  text-align: center;
  margin-bottom: 30px;
  position: relative;
}

.title {
  font-size: 22px;
  font-weight: 500;
  color: #333;
  letter-spacing: 1px;
  margin: 0;
  text-transform: uppercase;
  display: inline-block;
  padding: 0 20px;
}

.description {
  text-align: center;
  max-width: 900px;
  margin: 0 auto 50px;
  line-height: 1.6;
  font-size: 16px;
}

.amenitiesGrid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 40px;
  margin-bottom: 60px;
}

.amenityItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 110px;
}

.iconContainer {
  margin-bottom: 15px;
  transition: transform 0.3s ease;
}

.iconContainer:hover {
  transform: scale(1.1);
}

.amenityName {
  text-align: center;
  font-size: 14px;
  margin: 0;
  color: #333;
  font-weight: 500;
}

.imageContainer {
  position: relative;
  margin-bottom: 30px;
  border-radius: 8px;
  overflow: hidden;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
}

.featureImage {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

.ctaButton {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.experienceButton {
  background-color: white;
  color: #333;
  border: none;
  padding: 15px 30px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-transform: uppercase;
}

.experienceButton:hover {
  background-color: #f0f0f0;
}

.scheduleVisitContainer {
  position: fixed;
  right: 0;
  top: 50%;
  transform: translateY(-50%) rotate(-90deg);
  transform-origin: right bottom;
  z-index: 100;
}

.scheduleVisitButton {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  color: #333;
  padding: 10px 20px;
  font-size: 12px;
  letter-spacing: 1px;
  cursor: pointer;
  white-space: nowrap;
  text-transform: uppercase;
  transform-origin: center;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modalContent {
  background: white;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  max-width: 900px; /* Updated to accommodate the wider layout */
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  text-align: left; /* Updated for the new layout */
}

.closeButton {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 18px;
  background: none;
  border: none;
  cursor: pointer;
}

.modalImage {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin-top: 10px;
  max-width: 600px; /* Added to match the image size in the provided screenshot */
  display: block;
  margin: 0 auto 20px; /* Centered with margin */
}

.modalText {
  margin-top: 15px;
  font-size: 16px;
  color: #666;
  margin-bottom: 20px;
}

/* New styles for the carousel */
.carousel {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.prevButton,
.nextButton {
  background-color: #a68a64;
  color: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 1.5rem;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.prevButton {
  left: 10px;
}

.nextButton {
  right: 10px;
}

.prevButton:hover,
.nextButton:hover {
  background-color: #8a6f47;
}

.slideContent {
  text-align: left;
  padding: 20px;
}

.modalTitle {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  text-align: center;
  margin-bottom: 10px;
}

.modalSubtitle {
  font-size: 1.3rem;
  font-weight: bold;
  color: #a68a64;
  margin-bottom: 10px;
}

.categories {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.category {
  flex: 1;
}

.category h5 {
  font-size: 1.1rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

.category ul {
  list-style-type: disc;
  padding-left: 20px;
}

.category li {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 5px;
}

/* Existing media queries */
@media (max-width: 1024px) {
  .container {
    padding: 40px 15px;
  }

  .title {
    font-size: 20px;
  }

  .description {
    font-size: 15px;
    margin-bottom: 40px;
  }

  .amenitiesGrid {
    gap: 30px;
  }

  .amenityItem {
    width: 90px;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 30px 10px;
  }

  .title {
    font-size: 18px;
  }

  .description {
    font-size: 14px;
    margin-bottom: 30px;
  }

  .amenitiesGrid {
    gap: 20px;
  }

  .amenityItem {
    width: 80px;
  }

  .featureImage {
    border-radius: 5px;
  }

  .experienceButton {
    padding: 12px 25px;
    font-size: 12px;
  }

  .scheduleVisitContainer {
    display: none; /* Hide on mobile */
  }
}

@media (max-width: 480px) {
  .container {
    padding: 20px 5px;
  }

  .title {
    font-size: 16px;
  }

  .description {
    font-size: 13px;
  }

  .amenitiesGrid {
    gap: 15px;
  }

  .amenityItem {
    width: 70px;
  }

  .experienceButton {
    padding: 10px 20px;
    font-size: 10px;
  }
}

/* New media queries for the carousel */
@media (max-width: 768px) {
  .modalContent {
    padding: 15px;
    max-width: 90%;
  }

  .modalImage {
    max-width: 100%;
  }

  .categories {
    flex-direction: column;
    gap: 15px;
  }

  .prevButton,
  .nextButton {
    width: 30px;
    height: 30px;
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .modalTitle {
    font-size: 1.2rem;
  }

  .modalSubtitle {
    font-size: 1rem;
  }

  .modalText {
    font-size: 14px;
  }

  .category h5 {
    font-size: 1rem;
  }

  .category li {
    font-size: 0.8rem;
  }
}