.navbarWrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    max-width: 100%;
    z-index: 100;
    
    background: none;
    transition: background 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  }
  .navbarWrapper.scrolled {
    background: rgba(0, 0, 0, 0.9); /* Dark background on scroll */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}
  
  /* Top navbar styling */
  .topNavbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.15); /* More transparency */
    padding: 10px 30px;
    backdrop-filter: blur(8px); /* Increased blur effect */
  }
  
  .logo {
    height: 40px;
  }
  
  .logo img {
    height: 100%;
    object-fit: contain;
  }
  
  .navIcons {
    display: flex;
    gap: 14px;
  }
  
  .iconButton {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: white;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .iconButton:hover {
    background-color: rgba(255, 255, 255, 0.15);
  }
  
  /* Bottom navbar styling */
  .bottomNavbar {
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.25);
    padding: 0 30px;
    height: 55px;
    backdrop-filter: blur(8px);
    overflow-x: auto; 
  }
  
  .backButton {
    display: flex;
    align-items: center;
    background: none;
    border: none;
    color: white;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 1px;
    cursor: pointer;
    padding: 0;
    margin-right: 25px;
  }
  
  .backButton svg {
    margin-right: 6px;
  }
  
  .navLinks {
    display: flex;
    flex-wrap: nowrap;
    height: 100%;
    overflow-x: auto;
    scrollbar-width: none;
  }
  
  .navLinks::-webkit-scrollbar {
    display: none;
  }
  
  .navLink {
    display: flex;
    align-items: center;
    color: white;
    text-decoration: none;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 1px;
    padding: 0 18px;
    height: 100%;
    transition: background-color 0.3s;
    white-space: nowrap;
  }
  
  .navLink:hover {
    background-color: rgba(255, 255, 255, 0.15);
    cursor: pointer;
  }

  .contactBox {
    position: absolute;
    top: 60px;
    right: 10px;
    width: 250px;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    text-align: left;
    z-index: 1000;
}

.contactBox h3, .contactBox h4 {
    margin: 0;
    font-size: 1rem;
    font-weight: 700;
}

.contactBox p {
    font-size: 0.9rem;
    margin: 0.3rem 0;
}

  
  @media (max-width: 768px) {
    .topNavbar {
      padding: 8px 16px;
    }
    
    .bottomNavbar {
      padding: 0 16px;
      height: 45px;
    }
    
    .navLink {
      padding: 0 10px;
      font-size: 12px;
    }
    
    .backButton {
      font-size: 12px;
      margin-right: 14px;
    }

  }
