.pageContainer {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 15px;
}

.contentWrapper {
  display: flex;
  gap: 60px;
}

.leftSection {
  flex: 1;
}

.mainTitle {
  font-size: 2.5rem;
  color: #1f2937;
  line-height: 1.2;
  margin-bottom: 20px;
  font-weight: 700;
}

.description {
  color: #6b7280;
  font-size: 1rem;
  line-height: 1.6;
}

.rightSection {
  flex: 1;
}

.contactTitle {
  font-size: 1.5rem;
  color: #1f2937;
  margin-bottom: 30px;
  font-weight: 600;
}

.contactForm {
  display: flex;
  flex-direction: column;
}

.nameRow {
  display: flex;
  gap: 15px;
  margin-bottom: 15px;
}

.halfInput {
  flex: 1;
  padding: 12px;
  border: none;
  border-bottom: 1px solid #d1d5db;
  font-size: 1rem;
  color: #1f2937;
}

.fullInput {
  width: 100%;
  padding: 12px;
  border: none;
  border-bottom: 1px solid #d1d5db;
  font-size: 1rem;
  color: #1f2937;
}

.formGroup {
  margin-bottom: 15px;
}

.textarea {
  width: 100%;
  padding: 12px;
  border: none;
  border-bottom: 1px solid #d1d5db;
  font-size: 1rem;
  min-height: 100px;
  resize: vertical;
  color: #1f2937;
}

.privacyText {
  font-size: 0.75rem;
  color: #6b7280;
  margin-bottom: 15px;
}

.privacyLink {
  color: #3b82f6;
  text-decoration: none;
  margin: 0 3px;
}

.submitButton {
  background-color: #1f2d41;
  color: white;
  border: none;
  padding: 15px 25px;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
  align-self: flex-start;
  transition: background-color 0.3s ease;
}

.submitButton:hover {
  background-color: #2c3e50;
}

input:focus,
textarea:focus {
  outline: none;
  border-bottom: 1px solid #3b82f6;
}

@media (max-width: 768px) {
  .contentWrapper {
    flex-direction: column;
    gap: 30px;
  }

  .nameRow {
    flex-direction: column;
    gap: 15px;
  }
}