.homeContainer {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    /* Added padding-top to account for fixed navbar */
    padding-top: 0;
  }
  
  .heroSection {
    position: relative;
    width: 100%;
    height: 100%;
    background-image: url('../../Assets/Home.jpg');
    background-size: cover;
    background-position: center;
    color: white;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5));
  }
  
  .heroContent {
    position: absolute;
    bottom: 6rem;
    left: 0;
    width: 100%;
    padding: 0 2rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  
  .propertyInfo {
    max-width: 70%;
    background: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0));
    padding: 1rem;
    color: white;
    display: inline-block;
  }
  
  .propertyName {
    font-size: 2.5rem;
    font-weight: 700;
    margin: 0;
    line-height: 1.2;
  }
  
  .propertyLocation {
    font-size: 1.2rem;
    font-weight: 500;
    margin: 0.5rem 0 1.5rem;
  }
  
  .propertyDetails {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  .navLink {
    white-space: nowrap; /* Prevent text breaking awkwardly */
    font-size: 14px; /* Reduce font size for mobile */
}
  
  .propertyPrice, .propertyPossession, .propertyType {
    font-size: 1rem;
  }
  
  .propertySeparator {
    margin: 0 0.5rem;
    opacity: 0.7;
  }
  
 
  .scheduleVisit {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%) rotate(-90deg);
    transform-origin: right center;
    background-color: white;
    color: #333;
    padding: 0.8rem 1.5rem;
    font-weight: 600;
    font-size: 0.9rem;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    cursor: pointer;
    box-shadow: -2px 0 10px rgba(0, 0, 0, 0.1);
  }
  
  @media (max-width: 768px) {
    .homeContainer {
      padding-top: 90px;
    }
    
    .heroContent {
      padding: 0 1rem;
      bottom: 4rem;
    }
    
    .propertyName {
      font-size: 1.8rem;
    }
    
    .propertyLocation {
      font-size: 1rem;
      margin: 0.3rem 0 1rem;
    }
    
    .propertyDetails {
      font-size: 0.9rem;
    }
    
    .scheduleVisit {
      padding: 0.6rem 1.2rem;
      font-size: 0.8rem;
    }
  }